.navigatorContainer{
    margin-top : 40px;
    margin-bottom : 70px; 
}

.leftBtn{
    float : left;
}

.rightBtn{
    float : right;
}

.navigatorBtn{
    border : 1px solid lightgray;
    color : rgb(80, 164, 243);
    background-color : white;
    border-radius : 15px;
    padding : 5px 10px;   
    cursor : pointer; 
    width : 74px;
}
.navigatorBtn:focus{
    outline: none;
}
.searchContainer{
    margin: 30px 0 40px 0; 
    display: flex;
}

.searchInput{
    display : block;
    border : none;
    border-bottom : 1px solid lightgrey; 
    width: 300px; 
}

.paginationCount{
    margin-left: 20px;
}

.searchInput:focus{
    outline : none; 
}
table {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  td, th {
    border: 1px solid rgb(80, 164, 243);
    text-align: left;
    padding: 12px 8px;
  }
  th{
    background-color: #dddddd;
  }
  
  tr:nth-child(even) {
    background-color: #dddddd;
  }
.tableHeader{
    cursor : pointer;
}
.tableRow{
    cursor: pointer;
}

td a{
    color : #50a4f3;
}
.appContainer{
    width : 95%;
    margin : auto; 
}
.paginationBtn{
    margin : 0 1px;
    background-color : transparent;
    padding : 5px 30px;
    border : 1px solid lightgrey;
    color : #50a4f3;
    cursor: pointer;  
}
.activePaginationBtn{
    margin : 0 1px;
    background-color : #50a4f3;
    padding : 5px 30px;
    border : 1px solid lightgrey;
    color : white;
    cursor: pointer; 
}

button:focus{
    outline: none;
}
.detailedViewContainer{
    width : 95%;
    margin : auto; 
}

.nameField {
    padding : 0 15px;
    font-weight : 400;
}

.valueField{
    color : grey;
}

.personalDetail{
    display : flex;
    justify-content : space-between;
    padding : 0 15px;
    margin : 10px 0;    
}

.seperator{
    height : 1px;
    background-color : grey; 
}
